body {
    margin: 0;
    font-family: -apple-system, "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}
nav {
    background: none;
}
nav ul {
    display: flex;
    justify-content: center;
}
.pagination {
    margin-top: 5px;
    width: 100%;
}
